<script>
export default {
  data() {
    return {
      status: false,
      charging: null,
      blevel: 100,
      dischargingTime: null,
    };
  },

  created() {
    window.addEventListener("offline", function (e) {
      console.log("offline", e);
      navigator.vibrate(300);
      alert("your application is offline");
    });

    window.addEventListener("online", function (e) {
      console.log("online", e);

      alert("your application is online");
    });
    this.betteryStatus();
  },
  mounted() {
    // this.betteryStatus();
  },
  methods: {
    betteryStatus() {
      console.log(
        navigator.getBattery().then((battery) => console.log(battery))
      );
      navigator.getBattery().then((battery) => {
        this.charging = battery.charging;
        this.blevel = battery.level * 100;
        console.log(this.charging, this.blevel);
        // Update the battery status initially when the promise resolves ...
        // this.updateBatteryStatus(battery);

        // .. and for any subsequent updates.
        battery.onchargingchange = function () {
          this.updateBatteryStatus(battery);
        };

        battery.onlevelchange = function () {
          this.updateBatteryStatus(battery);
        };

        battery.ondischargingtimechange = function () {
          this.updateBatteryStatus(battery);
        };
      });
    },
    updateBatteryStatus(battery) {
      this.charging = battery.charging ? true : false;
      this.blevel = battery.level;
      this.dischargingTime = battery.dischargingTime / 60;
      console.log("check", this.charging, this.blevel, this.dischargingTime);
    },
    // makeToast(append = false) {
    //   this.toastCount++;
    //   this.$bvToast.toast(`This is toast number ${this.toastCount}`, {
    //     title: "BootstrapVue Toast",
    //     autoHideDelay: 5000,
    //     appendToast: append,
    //   });
    // },
  },
};
</script>
