export function random(min, max) {
  return Math.floor(Math.random() * (max - min + 1) + min);
}
/**
 * @description
 * @export
 * @param {*} num
 * @returns
 */
export function doubleDigit(num) {
  return num < 10 ? "0" + num : num;
}

export function readACL(unitID) {
  const foundAccess = _.find(JSON.parse(localStorage.getItem("acl")), function(
    o
  ) {
    return o.uID === unitID;
  });
  return foundAccess;
}

export function checkChildrenACL(menu) {
  const submenus = [];
  _.forEach(menu, function(submenu) {
    const subMenuRule = readACL(submenu.uID);
    if (subMenuRule && subMenuRule.canAccess === 1) {
      submenus.push(submenu);
    }
  });
  return submenus;
}

export function checkAccessControl(accessID) {
  let checkData = JSON.parse(localStorage.getItem("dummyData"));
  const foundAccess = checkData ? checkData.hasOwnProperty(accessID) : true;
  if (foundAccess) {
    return false;
  } else {
    return true;
  }
}

export function checkRouterAcl() {
  let routerControl = localStorage.getItem("dummyData");
  return JSON.parse(routerControl);
}

export function readDisplayAccess(unitID) {
  const foundAccess = _.find(JSON.parse(localStorage.getItem("acl")), function(
    o
  ) {
    return o.uID === unitID;
  });
  if (foundAccess) {
    return foundAccess.canAccess === 1 ? true : false;
  } else {
    return false;
  }
}

export function getDateHourMinute(date) {
  return (
    date.getFullYear() +
    "-" +
    doubleDigit(date.getMonth() + 1) +
    "-" +
    doubleDigit(date.getDate()) +
    " " +
    doubleDigit(date.getHours()) +
    ":" +
    doubleDigit(date.getMinutes()) +
    ":" +
    doubleDigit(date.getSeconds())
  );
}

export function dateOrTime(dateString, type) {
  /* check for ie 11 */
  dateString = ieDateTimeFormat(dateString);
  const date = new Date(dateString);
  switch (type) {
    case "date":
      return (
        date.getFullYear() +
        "-" +
        doubleDigit(date.getMonth() + 1) +
        "-" +
        doubleDigit(date.getDate())
      );
    case "hourMinute":
      return (
        doubleDigit(date.getHours()) + ":" + doubleDigit(date.getMinutes())
      );
    case "month": {
      return date.getMonth() + 1;
    }
    case "year": {
      return date.getFullYear();
    }
    case "hours": {
      return doubleDigit(date.getHours());
    }
    case "minutes": {
      return doubleDigit(date.getMinutes());
    }
    default:
      return "";
  }
}

export function timeDiff(start_time, end_time) {
  const startTime = new Date(start_time);
  const endTime = new Date(end_time);
  var diff = Math.abs(startTime - endTime);
  return Math.floor(diff / 1000 / 60) + i18n.t("MINUTE");
}

/**
 * Randomize array element order in-place.
 * Using Durstenfeld shuffle algorithm.
 */
export const shuffleArray = (array) => {
  for (let i = array.length - 1; i > 0; i--) {
    let j = Math.floor(Math.random() * (i + 1));
    let temp = array[i];
    array[i] = array[j];
    array[j] = temp;
  }
  return array;
};

export function getLangField(field) {
  let optionsNameField = i18n.locale === "ja" ? field + "_jp" : field + "_en";
  return optionsNameField;
}

export function getObjectfromArray(id, dataArray, key) {
  let obj = dataArray.find((data) => id === data[key]);
  return obj;
}

