import Vue from "vue";
import VueI18n from "vue-i18n";
import en from "./en.json";
import ja from "./ja.json";
Vue.use(VueI18n);

const messages = {
  en: en,
  ja: ja
};
let locale = "ja";
if (localStorage.getItem("locale")) {
  locale = localStorage.getItem("locale");
}
const i18n = new VueI18n({ locale: locale, fallbackLocale: "en", messages });

export default i18n;
