import Vue from "vue";
import Router from "vue-router";
// import { accessConstants } from "@/shared/accessConstants";
import { logValuesInSession, checkAccessControl } from "@/shared/utils";
//layout
const RootSidebarContainer = () => import("@/containers/RootSidebarContainer");
const DefaultContainer = () => import("@/containers/DefaultContainer");

//user login
const UserLogin = () => import("@/views/user/auth/UserLogin");
const UserResetPasswordRequest = () =>
  import("@/views/user/auth/UserResetPasswordRequest");
const UserResetPassword = () => import("@/views/user/auth/UserResetPassword");

//user pages
const StoreData=() => import("@/views/business/ofline");
const BasicInformation=() => import("@/views/business/SettingTabOne");
const OtherInformation=() => import("@/views/business/SettingTabThree");
const CompanyInformation=() =>  import("@/views/business/SettingTabTwo");
const ContractInformation=()=>import("@/views/business/ContractInformation");
//terms pages
const TermsOfService = () => import("@/views/business/termsOfService/Index");
const QAndA = () => import("@/views/business/qAndA/Index");
const Inquiry = () => import("@/views/business/inquiry/Index");
const InquiryCompleted = () =>
  import("@/views/business/inquiry/InquiryCompleted");

//admin Pages

const NotFound = () => import("@/views/NotFound");








Vue.use(Router);

// eslint-disable-next-line no-unused-vars
// eslint-disable-next-line no-unused-vars
const userRoutes = [
  { path: "/404", component: NotFound },
  { path: "*", redirect: "/404" },
  { path: "/", redirect: { name: "user_login" } },
  {
    path: "/login",
    name: "user_login",
    component: UserLogin,
    meta: { breadcrumb: "SCHEDULE", title: "KANBEI" },
  },
  {
    path: "/reset/password/request",
    name: "user_reset_password_request",
    component: UserResetPasswordRequest,
    meta: { breadcrumb: "SCHEDULE", title: "KANBEI" },
  },
  {
    path: "/reset/password/:token",
    name: "user_reset_password",
    component: UserResetPassword,
    meta: { breadcrumb: "SCHEDULE", title: "KANBEI" },
  },

  {
    path: "/",
    component: DefaultContainer,
    children: [
      {
        path: "termsandconditions",
        name: "terms_of_service",
        component: TermsOfService,
        meta: {
          title: "KANBEI",
        },
      },
      {
        path: "qna",
        name: "q_and_a",
        component: QAndA,
        meta: {
          title: "KANBEI",
        },
      },
      {
        path: "contact",
        name: "inquiry",
        component: Inquiry,
        meta: {
          title: "KANBEI",
        },
      },
      {
        path: "contact/complete",
        name: "contact_complete",
        component: InquiryCompleted,
        meta: {
          title: "KANBEI",
        },
      },
    ],
  },
  {
    path: "/",
    component: RootSidebarContainer,
    children: [
     //StoreData
      {
        path: "StoreData",
        name: "StoreData",
        component: StoreData,
        meta: {
          breadcrumb: [{ name: "StoreData" }],
          title: "KANBEI",
          auth: true,
          user: true,
        },
      },
     //BasicInformation
     {
      path: "BasicInformation",
      name: "BasicInformation",
      component: BasicInformation,
      meta: {
        breadcrumb: [{ name: "BasicInformation" }],
        title: "KANBEI",
        auth: true,
        user: true,
      },
    },
    //OtherInformation
    {
      path: "OtherInformation",
      name: "OtherInformation",
      component: OtherInformation,
      meta: {
        breadcrumb: [{ name: "OtherInformation" }],
        title: "KANBEI",
        auth: true,
        user: true,
      },
    },
    {
      path: "CompanyInformation",
      name: "CompanyInformation",
      component: CompanyInformation,
      meta: {
        breadcrumb: [{ name: "CompanyInformation" }],
        title: "KANBEI",
        auth: true,
        user: true,
      },
    },
    //ContractInformation
    {
      path: "ContractInformation",
      name: "ContractInformation",
      component: ContractInformation,
      meta: {
        breadcrumb: [{ name: "ContractInformation" }],
        title: "KANBEI",
        auth: true,
        user: true,
      },
    },
    ],
  },
];
/**
 * Client build(for Domain)
 * const baseUrl =  "/",
 *
 * NIS test build
 * const baseUrl = "/biz/";
 */

/**Please enable below two lines while generating business build */
let routes = userRoutes;

/**Please enable below two lines while generating admin build */
// const baseUrl = "/mng/";
// let routes = adminRoutes;

const router = new Router({
  /* toggle below two lines if  you want to remove # symbol and redired admin route to admin login */
  mode: "history",
  linkActiveClass: "open active",
  scrollBehavior: () => ({ y: 0 }),
  routes: routes,
});

// This callback runs before every route change, including on page load.
router.beforeEach((to, from, next) => {
  /** auth guards */
  if (to.meta.auth == true) {
    if (!localStorage.getItem("token")) {
      next("/login");
    } else {
      if (to.meta.admin == true && localStorage.getItem("role") == "USER") {
        next("/login"); // go to 'user/login';
      }
    }
  }
  if (to.meta.access_key) {
    const rule = checkAccessControl(to.meta.access_key);
    if (!rule) {
      return next({ name: "admin_login" });
    }
  }
  // This goes through the matched routes from last to first, finding the closest route with a title.
  // eg. if we have /some/deep/nested/route and /some, /deep, and /nested have titles, nested's will be chosen.
  const nearestWithTitle = to.matched
    .slice()
    .reverse()
    .find((r) => r.meta && r.meta.title);

  // Find the nearest route element with meta tags.
  const nearestWithMeta = to.matched
    .slice()
    .reverse()
    .find((r) => r.meta && r.meta.metaTags);

  // If a route with a title was found, set the document (page) title to that value.
  if (nearestWithTitle) document.title = nearestWithTitle.meta.title;

  // Remove any stale meta tags from the document using the key attribute we set below.
  let removedElements = Array.from(
    document.querySelectorAll("[data-vue-router-controlled]")
  ).map((el) => el.parentNode.removeChild(el));
  logValuesInSession("removedElements", removedElements);
  // Skip rendering meta tags if there are none.
  if (!nearestWithMeta) return next();

  // Turn the meta tag definitions into actual elements in the head.
  nearestWithMeta.meta.metaTags
    .map((tagDef) => {
      const tag = document.createElement("meta");

      Object.keys(tagDef).forEach((key) => {
        tag.setAttribute(key, tagDef[key]);
      });

      // We use this to track which meta tags we create, so we don't interfere with other ones.
      tag.setAttribute("data-vue-router-controlled", "");

      return tag;
    })
    // Add the meta tags to the document head.
    .forEach((tag) => document.head.appendChild(tag));

  next();
});
export default router;
